
import { defineComponent } from 'vue'
import BackToHomeButtonVue from './components/BackToHomeButton.vue'

export default defineComponent({
  setup() {
    return {}
  },
  components: {
    BackToHomeButtonVue
  },
  computed: {
    isHome: function() {
      return this.$route.name == 'home'
    },
    isFullPage: function() {
      return this.$route.fullPath.split('/')[1] == 'full';
    }
  }
})
